.main {
    position: fixed;
    bottom: 30px;
    right: 30px;
    width: 50px;
    height: 50px;
    background: rgba(255, 255, 255, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    border: 1px solid rgba(110, 7, 243, 0.3);
    cursor: url("./../../assets/images/pointer.svg"), auto;
    opacity: 0;
    transform: scale(0.8);
    visibility: hidden;
    transition-duration: 0.4s;
    z-index: 10;
    box-sizing: border-box;
    box-shadow: 0px 0px 4px 2px rgba(110, 7, 243, 0.3);
    svg, path {
        cursor: url("./../../assets/images/pointer.svg"), auto;
    }
}
  
.visible {
  opacity: 1;
  transform: scale(1);
  visibility: visible;
}


@media (min-width: 700px) {
    .main {
        &:hover {
            transform: scale(1.2);
        }
    }
}